import AddRoundedIcon from "@mui/icons-material/AddRounded";
import CloseRoundedIcon from "@mui/icons-material/CloseRounded";
import DeleteOutlineRoundedIcon from "@mui/icons-material/DeleteOutlineRounded";
import EditOutlinedIcon from "@mui/icons-material/EditOutlined";
import {
  Box,
  Button,
  Divider,
  IconButton,
  Paper,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import Chip from "@mui/material/Chip";
import React, { useContext, useState, createContext, useEffect } from "react";
import GridFactory from "../../../Tools/GridFactory";
import { StatusContext } from "../../../App";
import CheckRoundedIcon from "@mui/icons-material/CheckRounded";
import AddTourDialog from "./AddTourDialog";
import noTour from "../../../assets/pics/noTour.svg";
export const ToursContext = createContext();
export default function Tours() {
  const [addDialog, setaddDialog] = useState(false);
  const [toursData, settoursData] = useState([]);

  useEffect(() => {
    settoursData([...DefaultTours]);
  }, []);

  const DeleteToure = (Tindex) => {
    const res = toursData.filter((value, index) => Tindex !== index);
    settoursData([...res]);
  };

  const AddToure = (toure) => {
    settoursData([...toursData, toure]);
  };
  const AddCityToTour = (Tindex, text) => {
    const res = toursData.map((toure, index) => {
      if (index === Tindex) toure.Cities = [...toure.Cities, text];
      return toure;
    });
    settoursData([...res]);
  };

  const DeleteCityFromTour = (Tindex, Cindex) => {
    const res = toursData.map((toure, index) => {
      if (index === Tindex)
        toure.Cities = toure.Cities.filter((city, index) => Cindex !== index);
      return toure;
    });
    settoursData([...res]);
  };

  const EditCname = (Tindex, Cindex, text) => {
    const res = toursData.map((toure, index) => {
      if (index === Tindex)
        toure.Cities = toure.Cities.map((city, index) => {
          if (Cindex === index) city = text;
          return city;
        });
      return toure;
    });
    settoursData([...res]);
  };

  const OpenDialog = () => {
    setaddDialog(true);
  };
  const CloseDialog = () => {
    setaddDialog(false);
  };
  return (
    <ToursContext.Provider
      value={{
        toursData,
        DeleteToure,
        AddToure,
        AddCityToTour,
        DeleteCityFromTour,
        EditCname,
      }}
    >
      <Paper sx={classes.root}>
        <AddButton OpenDialog={OpenDialog} />
        {toursData.length > 0 ? <Cards /> : <NoTours />}
        <AddTourDialog CloseDialog={CloseDialog} addDialog={addDialog} />
      </Paper>
    </ToursContext.Provider>
  );
}

const NoTours = () => {
  return <Box sx={classes.NoTours} />;
};

const Cards = () => {
  const { toursData } = useContext(ToursContext);
  return (
    <GridFactory
      items={toursData}
      gridSize={gridSize}
      child={<TourCard />}
      spacing={2}
    />
  );
};
const AddButton = ({ OpenDialog }) => {
  return (
    <Stack
      direction="row"
      justifyContent="flex-end"
      sx={{ marginBottom: { xs: ".5rem", md: "1.5rem" } }}
    >
      <Button
        variant="outlined"
        startIcon={<AddRoundedIcon />}
        sx={classes.addButton}
        onClick={OpenDialog}
      >
        Add Toure
      </Button>
    </Stack>
  );
};

const TourCard = ({ item, Tindex }) => {
  const { dispatch } = useContext(StatusContext);
  const { DeleteToure, AddCityToTour, EditCname, toursData } =
    useContext(ToursContext);

  const createModeArray = () => {
    let res = [];
    res = item.Cities.map((city, index) => {
      return { mode: "edit", value: city };
    });
    return res;
  };

  const [mode, setmode] = useState(createModeArray() ?? []);

  const handleChange = (e) => {
    let res = mode?.map((item, index) => {
      if (index == e.target.name) item.value = e.target.value;
      return item;
    });
    setmode([...res]);
  };
  // Edit Citie Mode
  const ChangeMode = (index) => {
    let res = mode ?? [];
    res[index].Editmode =
      res[index] && res[index].Editmode && res[index].Editmode === "submit"
        ? "edit"
        : "submit";
    setmode([...res]);
    dispatch({
      type:
        res[index].Editmode && res[index].Editmode === "submit"
          ? "showInfo"
          : "showSuccess",
      payload:
        res[index].Editmode && res[index].Editmode === "submit"
          ? "Edit Mode"
          : "Submited",
    });
  };

  const AddCity = () => {
    dispatch({
      type: "showDialog",
      payload: {
        title: "Add City",
        type: "info",
        field: { label: "Enter City Name", required: true },
        buttonLabel: "ADD",
        callback: (enteredText) => {
          AddCityToTour(Tindex, enteredText);
        },
      },
    });
  };

  const HandleEdit = (index) => {
    if (!mode[index])
      mode[index] = { ...mode[index], value: toursData[Tindex].Cities[index] };
    if (mode && mode[index].Editmode && mode[index].Editmode === "submit") {
      if (mode && mode[index].value !== "") {
        dispatch({
          type: "showDialog",
          payload: {
            title: "Edit this City",
            // message: "City Name: Neumarkt",
            type: "info",
            buttonLabel: "EDIT",
            callback: () => {
              EditCname(Tindex, index, mode[index].value);
              ChangeMode(index);
            },
          },
        });
      }
    } else ChangeMode(index);
  };
  const HandleDelete = () => {
    dispatch({
      type: "showDialog",
      payload: {
        title: "Delete this Toure",
        message: "Toure name: A",
        type: "negative",
        buttonLabel: "Delete",
        callback: () => DeleteToure(Tindex),
      },
    });
  };
  return (
    <Box sx={classes.card}>
      <Box>
        <Stack direction="row" justifyContent="space-between">
          <Typography paragraph sx={classes.toureName}>
            Toure: {item.Tname}
          </Typography>
          <CloseRoundedIcon
            sx={{ color: "primary.main", cursor: "pointer" }}
            onClick={HandleDelete}
          />
        </Stack>
        <Stack direction="column" spacing={2}>
          {item?.Cities?.map((city, index) => (
            <Stack direction="column" spacing={2} key={index}>
              <EditCityName
                mode={mode}
                HandleEdit={HandleEdit}
                index={index}
                city={city}
                Tindex={Tindex}
                handleChange={handleChange}
              />
              {index + 1 < item?.Cities?.length ? <Divider /> : false}
            </Stack>
          ))}
        </Stack>
      </Box>
      <AddSubmit status={item.status} AddCity={AddCity} />
    </Box>
  );
};

const EditCityName = ({
  mode,
  HandleEdit,
  index,
  city,
  Tindex,
  handleChange,
}) => {
  return (
    <Stack
      key={index}
      direction="row"
      justifyContent="space-between"
      alignItems="center"
    >
      {mode[index] && mode[index].Editmode === "submit" ? (
        <TextField
          variant="standard"
          color="secondary"
          name={index.toString()}
          onChange={handleChange}
          value={mode[index].value ?? ""}
        />
      ) : (
        <Chip label={city} color="secondary" variant="outlined" />
      )}
      <EditDelete
        mode={mode}
        HandleEdit={HandleEdit}
        index={index}
        Tindex={Tindex}
      />
    </Stack>
  );
};

const AddSubmit = ({ status, AddCity }) => {
  const { dispatch } = useContext(StatusContext);
  const HandleStart = () => {
    dispatch({
      type: "showDialog",
      payload: {
        title: "Start This Toure",
        message: "Toure name: A",
        type: "positive",
        buttonLabel: "START",
        callback: () => console.log("ok"),
      },
    });
  };
  const HandleDectivate = () => {
    dispatch({
      type: "showDialog",
      payload: {
        title: "Stop This Toure",
        message: "Toure name: A",
        type: "negative",
        buttonLabel: "STOP",
        callback: () => console.log("ok"),
      },
    });
  };
  return (
    <Stack direction="column" spacing={2} sx={{ pt: "1rem" }}>
      <Button
        variant="outlined"
        startIcon={<AddRoundedIcon />}
        color="third"
        onClick={AddCity}
      >
        Add City
      </Button>
      {status === true ? (
        <Button variant="contained" color="error" onClick={HandleDectivate}>
          Stop Toure
        </Button>
      ) : (
        <Button variant="contained" color="success" onClick={HandleStart}>
          Start Toure
        </Button>
      )}
    </Stack>
  );
};

const EditDelete = ({ mode, HandleEdit, index, Tindex }) => {
  const { dispatch } = useContext(StatusContext);
  const { DeleteCityFromTour } = useContext(ToursContext);

  const HandleDelete = () => {
    if (mode && mode[index].Editmode !== "submit")
      dispatch({
        type: "showDialog",
        payload: {
          title: "Delete this City",
          message: "City Name : Regensbure",
          type: "negative",
          buttonLabel: "DELETE",
          callback: () => DeleteCityFromTour(Tindex, index),
        },
      });
    else
      dispatch({
        type: "showError",
        payload: "Please Submit Data before Deleting",
      });
  };
  return (
    <Stack direction="row" spacing={1}>
      <IconButton color="primary" onClick={HandleDelete}>
        <DeleteOutlineRoundedIcon />
      </IconButton>
      <IconButton
        name={index}
        color={
          mode[index] && mode[index].Editmode === "submit"
            ? "success"
            : "secondary"
        }
        onClick={() => HandleEdit(index)}
      >
        {mode[index] && mode[index].Editmode === "submit" ? (
          <CheckRoundedIcon />
        ) : (
          <EditOutlinedIcon />
        )}
      </IconButton>
    </Stack>
  );
};

